window.addEventListener('DOMContentLoaded', () => {
    fix100vh();
    window.addEventListener('resize', () => {
        fix100vh();
        getScrollBarSize()
    })

    getScrollBarSize()

})

function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

let chainSelect = document.querySelector(".chain-select")
let chainModal = document.querySelector("#chainModal")
if(chainSelect){
    chainSelect.onclick = function () {
        chainModal.classList.add("_active")
    }

    let closeButtons = chainModal.querySelectorAll("._close-chain-modal")
    closeButtons.forEach(close => {
        close.onclick = function () {
            chainModal.classList.remove("_active")
        }
    })

}
